import React, { Component } from "react";
import { Tabs } from "antd";
import { SmileOutlined, UnlockOutlined } from "@ant-design/icons";

import ChangePassword from "./Tabs/ChangePassword";
import ChangeComponentsSize from "./Tabs/ChangeComponentsSize";

const { TabPane } = Tabs;

class settingForm extends Component {
    state = {
        size: localStorage.getItem("size"),
    };

    onChangeSize = () => {
        this.setState({ size: localStorage.getItem("size") });
    };

    render() {
        const { size } = this.state;

        return (
            <div>
                <Tabs defaultActiveKey="1">
                    <TabPane
                        forceRender={true}
                        tab={
                            <span>
                                <SmileOutlined />
                                Tamao
                            </span>
                        }
                        key="1"
                    >
                        <ChangeComponentsSize componentSize={size} callback={this.onChangeSize} />
                    </TabPane>
                    <TabPane
                        forceRender={true}
                        tab={
                            <span>
                                <UnlockOutlined />
                                Contrasea
                            </span>
                        }
                        key="2"
                    >
                        <ChangePassword componentSize={size} />
                    </TabPane>
                </Tabs>
            </div>
        );
    }
}

export default settingForm;
